.get-started-section {
    /* Style for "Mask" */
    position: relative;
    background-color: #f67e7e;
    .get-started-container {
        display: flex;
        padding-top: 78px;
        padding-bottom: 71px;
        margin-left: 263px;
        gap: 260px;
    }
    .title {
        /* Style for "Ready to g" */
        width: 504px;
        height: 53px;
        color: #012f34;
        font-family: "Livvic-Bold";
        font-size: 48px;
        font-weight: 700;
        font-style: normal;
        letter-spacing: normal;
        line-height: 48px;
        text-align: left;
    }
    button {
        /* Style for "Rectangle" */
        width: 153px;
        height: 48px;
        border-radius: 24px;
        background-color: rgba(245, 222, 179, 0);

        border: 2px solid #012f34;
        span {
            /* Style for "contact us" */
            width: 88px;
            height: 28px;
            color: #012f34;
            font-family: "Livvic-SemiBold";
            font-size: 18px;
            font-weight: 600;
            font-style: normal;
            letter-spacing: normal;
            line-height: 28px;
            text-align: left;
        }
    }
    .shape7 {
        width: 200px;
        height: 200px;
        position: absolute;
        bottom: -100px;
        left: 0;
        transform: rotate(90deg);
        z-index: 10;
    }
    .shape6 {
        height: 200px;
        position: absolute;
        width: 200px;
        bottom: 0;
        left: 0;
        width: 100px;
        height: 100px;
        transform: rotate(180deg);
    }
    .shape2 {
        width: 100px;
        height: 44px;
        position: absolute;
        bottom: 0;
        left: 0;
        transform: rotate(180deg);
    }
    .shape5 {
        width: 53px;
        height: 44px;
        position: absolute;
        bottom: 101px;
        left: 0;
    }
}
