footer {
    background-color: #002529;
    position: relative;
    .footer-container {
        margin-top: 48px;
        margin-bottom: 54px;
        justify-content: space-between;
        background-color: #002529;
        .name {
            display: flex;
            flex-direction: column;
            gap: 25px;

            span {
                color: #ffffff;
                font-family: "Livvic-SemiBold";
                font-weight: 600;
                font-style: normal;
                letter-spacing: normal;
                line-height: 28px;
                text-align: left;
                margin-right: 80px;
                font-size: 40px;
            }
            .nav > * {
                width: 49px;
                height: 28px;
                color: #ffffff;
                font-family: "Livvic-SemiBold";
                font-size: 18px;
                font-weight: 600;
                font-style: normal;
                letter-spacing: normal;
                line-height: 28px;
                text-align: left;
            }
            :first-child {
                margin-right: 40px;
            }
        }
        .info {
            margin-right: 162px;
            & > * {
                //width: 445px;
                //height: 94px;
                opacity: 0.6;
                color: #ffffff;
                font-family: "Livvic - Semi Bold";
                font-size: 15px;
                font-weight: 600;
                font-style: normal;
                letter-spacing: normal;
                line-height: 25px;
                text-align: left;
            }
        }
        .copyrigths {
            display: flex;
            gap: 53px;
            flex-direction: column;
            .img-continer {
                gap: 16px;
                display: flex;
                align-self: flex-end;
                img {
                    width: 24px;
                    height: 20px;
                    object-fit: cover;
                }
            }
            .txt {
                /* Style for "Copyright" */
                width: 255px;
                height: 25px;
                opacity: 0.6;
                color: #ffffff;
                font-family: "Livvic - Semi Bold";
                font-size: 15px;
                font-weight: 600;
                font-style: normal;
                letter-spacing: normal;
                line-height: 25px;
                text-align: right;
            }
        }
    }
}
