.second {
    position: relative;
    background-color: #012f34;
    .main-container {
        background-color: #012f34;
        padding-top: 198px;
        padding-bottom: 143px;
    }
    .main-conatiner-second {
        background-color: #012f34;
        display: flex;
        gap: 141px;
        .title {
            /* Style for "Build & ma" */
            color: #ffffff;
            font-family: "Livvic-Bold";
            font-size: 48px;
            font-weight: 700;
            font-style: normal;
            letter-spacing: normal;
            line-height: 48px;
            text-align: left;
        }
        .card-container {
            gap: 35px;
            display: flex;
            flex-direction: column;

            .card-preview {
                display: grid;
                grid-template-columns: 0.5fr 1fr;
                grid-template-rows: 1fr 2fr;
                justify-items: center;
                align-items: center;
                column-gap: 23px;

                .img-container {
                    grid-column: 1/1;
                    grid-row: 1/-1;
                    position: relative;
                    img {
                        position: absolute;
                        top: -14px;
                        right: 22px;
                        width: 42px;
                        height: 56px;
                    }
                    .circle {
                        width: 56px;
                        height: 56px;
                        background-color: #2c6269;
                        border-radius: 101px;
                    }
                }
                .title {
                    /* Style for "Experience" */
                    width: 445px;
                    height: 28px;
                    color: #f67e7e;
                    font-family: "Livvic-Bold";
                    font-size: 18px;
                    font-weight: 700;
                    font-style: normal;
                    letter-spacing: normal;
                    line-height: 28px;
                    text-align: left;
                }
                .desc {
                    /* Style for "Our networ" */
                    width: 445px;
                    height: 47px;
                    opacity: 0.8;
                    color: #ffffff;
                    font-family: "Livvic-SemiBold";
                    font-size: 15px;
                    font-weight: 600;
                    font-style: normal;
                    letter-spacing: normal;
                    line-height: 25px;
                    text-align: left;
                }
            }
        }
    }
    img{
        height: 20px;
    }
    .shape6 {
        height: 200px;
        position: absolute;
        width: 200px;
        bottom: 100px;
        right: 0;
        width: 100px;
        height: 100px;
        transform: rotate(180deg);
    }
    .shape2 {
        width: 100px;
        height: 44px;
        position: absolute;
        bottom: 56px;
        right: 0;
        z-index: 15;
    }
    .shape5 {
        width: 53px;
        height: 44px;
        position: absolute;
        bottom: 201px;
        right: 47px;
    }
    .shape7 {
        width: 200px;
        height: 200px;
        position: absolute;
        bottom: 0;
        //right: -100px;
    }
}

.shape6 {
    height: 200px;
    position: absolute;
    width: 200px;
    bottom: 100px;
    right: 0;
    width: 100px;
    height: 100px;
    transform: rotate(180deg);
}
