.thread {
    background-color: #004047;
    .main-container {
        background-color: #004047;
        padding-top: 140px;
        padding-bottom: 151px;
        .title {
            color: #ffffff;
            font-family: "Livvic-Bold";
            font-size: 48px;
            font-weight: 700;
            font-style: normal;
            letter-spacing: normal;
            line-height: 48px;
            text-align: center;
            color: #ffffff;
            font-style: normal;
            letter-spacing: normal;
            line-height: normal;
            margin: 0 auto;
            background-color: #004047;
            margin-bottom: 88px;
            .color {
                color: #79c8c7;
                font-style: normal;
                letter-spacing: normal;
                line-height: normal;
            }
        }
        #content1::after {
            content: "\a";
            white-space: pre;
        }
        .card-person-container {
            display: flex;
            gap: 30px;
            .card-preview {
                display: flex;
                flex-direction: column;
                align-items: center;

                .person-desc {
                    position: relative;
                    margin-bottom: 27px;
                    height: 94px;
                    img {
                        width: 67px;
                        height: 56px;
                        position: absolute;
                        left: 0;
                        right: 0;
                        margin-left: auto;
                        margin-right: auto;
                        top: -41px;
                    }
                    .txt {
                        padding: 0px;
                        color: #ffffff;
                        font-family: "Livvic-SemiBold";
                        font-size: 14px;
                        font-weight: 600;
                        font-style: normal;
                        letter-spacing: normal;
                        line-height: 25px;
                        text-align: center;
                        z-index: 5;
                        max-width: 350px;
                        height: 96px;
                        position: relative;
                    }
                    
                }
                .name {
                    //width: 350px;
                    height: 28px;
                    color: #79c8c7;
                    font-family: "Livvic-Bold";
                    font-size: 18px;
                    font-weight: 700;
                    font-style: normal;
                    letter-spacing: normal;
                    line-height: 28px;
                    text-align: center;
                    margin-bottom: 2px;
                }
                .work-desc {
                    /* Style for "Product Ma" */
                    //width: 350px;
                    height: 18px;
                    color: #ffffff;
                    font-family: "Livvic-MediumItalic";
                    font-size: 13px;
                    font-weight: 500;
                    font-style: normal;
                    letter-spacing: normal;
                    line-height: 18px;
                    text-align: center;
                    margin-bottom: 2px;
                }
                img {
                    width: 110px;
                    height: 110px;
                }
            }
        }
    }
}
