nav {
    margin-top: 73px;
    margin-bottom: 133px;
    display: flex;
    justify-content: space-between;
    .aside {
        display: flex;
        align-items: center;
        &.left {
            & > * {
                width: 49px;
                height: 28px;
                color: #ffffff;
                font-family: "Livvic-SemiBold";
                font-size: 18px;
                font-weight: 600;
                font-style: normal;
                letter-spacing: normal;
                line-height: 28px;
                text-align: left;
            }
            :first-child {
                width: unset;
                height: unset;
                margin-right: 80px;
                font-size: 40px;
            }
            :nth-child(2) {
                margin-right: 40px;
            }
        }

        &.right {
            button {
                /* width: 147px; */
                /* height: 33px; */
                padding: 5px 24px;
                border-radius: 24px;
                border: 2px solid #ffffff;
                background-color: rgba(156, 121, 156, 0);
                span {
                    //width: 88px;
                    //height: 28px;
                    color: #ffffff;
                    font-family: "Livvic-SemiBold";
                    font-size: 18px;
                    font-weight: 600;
                    font-style: normal;
                    letter-spacing: normal;
                    /* line-height: 28px; */
                    text-align: left;
                    //margin-top: 50px;
                    //position: relative;
                    //top: -3px;
                }
            }
        }
    }
}
