.hero {
    position: relative;
    .grid-container-hero {
        display: grid;
        grid-template-columns: 1fr 395px;
        grid-template-rows: 79px 109px;
        margin-bottom: 158px;
        column-gap: 30px;
        .title {
            grid-column: 1/1;
            grid-row: 1/-1;
            /* Style for "Find the b" */
            //width: 635px;
            //height: 201px;
            color: #ffffff;

            font-family: "Livvic-Bold";
            font-size: 100px;
            font-weight: 700;
            font-style: normal;
            letter-spacing: normal;
            line-height: 100px;
            text-align: left;
            span {
                color: #f67e7e;
                font-style: normal;
                letter-spacing: normal;
                margin-left: 30px;
                //line-height: normal;
            }
        }
        .desc {
            grid-column: 2/3;
            grid-row: 2/3;
            .desc-txt {
                /* Style for "Finding th" */
                max-width: 445px;
                height: 109px;
                color: #ffffff;
                font-family: "Livvic - Semi Bold";
                font-size: 18px;
                font-weight: 600;
                font-style: normal;
                letter-spacing: normal;
                line-height: 28px;
                text-align: left;
            }
            .rectangle {
                width: 50px;
                height: 4px;
                background-color: #79c8c7;
                position: relative;
                top: -73px;
            }
        }
    }
    .shape1,
    .shape4 {
        width: 201px;
        height: 200px;
        position: absolute;
        bottom: -258px;
        height: 200px;
        right: 5px;
    }
    .shape1 {
        right: 31px;
        z-index: 5;
    }
    .shape2 {
        z-index: 10;
        width: 100px;
        height: 43px;
        position: absolute;
        bottom: -108px;
        transform: rotate( 
    180deg);
        /* background-color: #79c8c7; */
        right: 277px;
    }
    .shape3 {
        width: 200px;
        position: absolute;
        bottom: -259px;
        height: 200px;
        right: 160px;
    }
    .shape5 {
        z-index: 10;
        width: 43px;
        height: 43px;
        position: absolute;
        bottom: -112px;
        transform: rotate(180deg);
        background-color: #79c8c7;
        right: 0;
    }
}
